    import { Splide, SplideSlide, SplideTrack } from "@splidejs/react-splide";
    import { Card, Table, Button } from "react-bootstrap";
    import "../../assets/css/slide.scss";
    import React, { Suspense, useState } from "react";  
    import ConnectButton from "../../layout/common/connectButton";
    import AuthorInfo from "./authorInfo";
    import NoData from "../../layout/common/NoData";
    import PostMedia from "../../layout/common/postMedia";
    import SeeMoreModal from "../../layout/common/SeeMoreModal";
import { getEventVoicesMore, getwallRecordMostActiveUsersMore } from "../../../../../actions/analyticsNew";

    const LeaderbordPopup = React.lazy(() =>
     import("../../layout/popup/LeaderbordPopup")
     );
  
    const TopeventSekleton = React.lazy(() =>
        import("../../layout/skeleton/TopeventSekleton")
        );

    const TopeventVoice = (props) => {
    const settings = {
        type: "slide",
        rewind: false,
        speed: 1000,
        loop: true,
        interval: 7000,
        drag: true,
        perPage: 4,
        gap: 5,
        autoplay: false,
        padding: "0",
        pagination: false,
        arrows: true,
    };

    const [show, setShow] = useState(false)

    const handlePopup = event => {  
      setShow(event)
    }

    const { topeventVoices, top_event_loader } = props;

    return (
        <>
        <Card className="sw-card-custom gutter-b card-shadow mb-5">
        <Card.Header className="px-5 pt-5 border-0 pb-0 d-flex align-items-center justify-content-between">
            <h6 class="card-label font-weight-bolder m-0">Top Event Voices</h6>
            { topeventVoices.total>5 ?  <Button onClick={()=>handlePopup(true)} variant="link" className="p-0">See More</Button> : null }
        </Card.Header>
          <Card.Body className="py-5 px-0">
            <Table className="mb-0" responsive>
            <thead>
                <tr>
                <th style={{ width: "10%" }}>
                    S No.
                </th>
                <th style={{ width: "30%" }}>
                    Name
                </th>
                <th style={{ width: "15%" }}>
                    No. of Followers
                </th>
                <th style={{ width: "20%" }}>
                    Recent Posts
                </th>
                <th style={{ width: "10%" }}></th>
                </tr>
            </thead>
            <tbody>
                { top_event_loader ? <TopeventSekleton/> : topeventVoices && Object.keys(topeventVoices.data).length
                ? topeventVoices.data.map((data,key) => {
                    return (
                        <tr className="border-top">
                            <td>
                                <strong>{key+1}</strong>
                            </td>
                        <td>
                          <AuthorInfo picture={data.image} username={data.author.name} isIcon={data.network.icon} />
                        </td>
                        <td>
                            <strong>{data.postCount}</strong>
                        </td>
                        <td>

                         { data.postsData && Object.keys(data.postsData).length && data.postsData.filter(obj => obj.postFile!="").length ?                                 
                            <Splide
                            className="recent-post m-0"
                            hasTrack={false}
                            options={settings}
                            >
                            <SplideTrack>
                              { data.postsData.map((asset, index) => {
                                return (
                                    <SplideSlide key={index}>
                                       <PostMedia postFile={asset.postFile} postId={asset.postId} className={'img-fluid'} />
                                    </SplideSlide>
                                );
                                })}
                            </SplideTrack>
                            </Splide> : null }
                        </td>
                        <td>
                          <ConnectButton link={data.links}/>
                        </td>
                        </tr>
                    );
                    })
                  : <NoData status={1} col={5}/>
                }
            </tbody>
            </Table>
        </Card.Body>
        </Card>

       {topeventVoices.total>5 ? 
        <SeeMoreModal size={'lg'} showHide={show} handlePopup={handlePopup} title={'Leader Bord'}>
        <Suspense fallback={<></>}>
           <LeaderbordPopup moreData={getEventVoicesMore} {...props} />
        </Suspense>  
        </SeeMoreModal> : null }
        </>
    );
    };

    export default React.memo(TopeventVoice);;
